import { Component, h, State } from '@stencil/core';

/**
 * Items with icons only
 * For an improved accessibility, you are required to
 * provide a `text` for each item in the breadcrumbs.
 * However, in some UIs, the design might require
 * hiding the text and relying on an icon to visualize
 * an item in the path.
 *
 * In this case you can set the `iconOnly` to
 * `true` on the desired items.
 *
 * :::note
 * The last item (current step) will always
 * display both an icon and the text.
 * :::
 */
@Component({
    tag: 'limel-example-breadcrumbs-icon-only',
    shadow: true,
})
export class BreadcrumbsExampleIconOnly {
    @State()
    private items = [
        {
            text: 'Home',
            icon: 'home',
            iconOnly: true,
        },
        {
            text: 'Products',
        },
        {
            text: 'Phone',
        },
        {
            text: 'Accessories',
        },
    ];

    public render() {
        return <limel-breadcrumbs items={this.items} />;
    }
}
